<template>
  <div class="tabbar-item" @click="changeRoute()">
    <img :src="trueSrc" class="tabbar-item-img" />
    <p class="tabbar-item-title" :style="fontColor">{{ title }}</p>
  </div>
</template>

<script>
export default {
  name: 'TabBarItem',
  props: {
    // 
    title: String,
    // 
    imgSrc: String,
    // 
    imgSrcCurrent: String,
    //  
    path: {
      type: String,
      required: true
    }
  },
  computed: {
    trueSrc() {
      return this.isCurrent ? this.imgSrcCurrent : this.imgSrc
    },
    fontColor() {
      // 
      if (!this.isCurrent) {
        return { color: '#000' }
      }
      // 
      return this.$parent.$props.currentItemColor
        ? { color: this.$parent.$props.currentItemColor }
        : { color: '#f00' }
    },
    // item
    isCurrent() {
      return this.$route.path.indexOf(this.path) !== -1
    }
  },
  methods: {
    // 
    changeRoute() {
      if (this.$route.path.indexOf(this.path) === -1) {
        this.$router.push(this.path)
      }
    }
  }
}
</script>

<style lang='less' scoped>
.tabbar-item {
  flex: 1;
  text-align: center;
}

.tabbar-item-img {
  margin-top: 5px;
  width: 25px;
  height: 25px;
  vertical-align: bottom;
}

.tabbar-item-title {
  font-size: 12px;
  line-height: 16px;
}
</style>
