<template>
  <scroll class="right-scroll" ref="scroll">
    <div class="categroy-body-right">
      <ul class="sub-category">
        <li v-for="item in subCategory" :key="item.acm" class="sub-category-item">
          <a :href="item.link" class="sub-category-link">
            <img :src="item.image" alt="" class="sub-category-img" />
            <span class="sub-category-title">{{ item.title }}</span>
          </a>
        </li>
      </ul>
      <p class="dividing-line"></p>
      <goods-list :prop="cateDetail" @load="loadImg"></goods-list>
      <p class="under-line"></p>
    </div>
  </scroll>
</template>

<script>
import Scroll from '@/components/common/scroll'
import GoodsList from '@/components/content/goodsList'

import { mapState, mapMutations } from 'vuex'
export default {
  components: { GoodsList, Scroll },
  computed: {
    ...mapState({
      subCategory: (state) => state.category.subCategory,
      cateDetail: (state) => state.category.cateDetail,
      changed: (state) => state.category.changed
    })
  },
  methods: {
    ...mapMutations(['markChanged']),
    loadImg() {
      this.$refs.scroll.refresh()
    }
  },
  watch: {
    changed(val) {
      if (val) {
        this.$refs.scroll.scrollTo(0, 0)
        this.markChanged(false)
      }
    }
  }
}
</script>

<style lang='less' scoped>
.right-scroll {
  overflow: hidden;
  margin-left: 25%;
}
.categroy-body-right {
  .sub-category {
    width: 100%;
    overflow: hidden;
    flex-wrap: wrap;
    .sub-category-item {
      float: left;
      width: 33%;
      .sub-category-link {
        display: block;
        width: 100%;
        height: 100%;
        text-align: center;
        .sub-category-img {
          display: block;
          width: 75%;
          height: 75%;
          margin: 10px auto;
        }
      }
    }
  }
  .dividing-line {
    position: relative;
    width: 100%;
    height: 20px;
    border-top: 1px solid #ccc;
    margin-top: 30px;
  }
  .dividing-line::before {
    content: '';
    font-size: 16px;
    color: #ccc;
    background-color: #fff;
    position: absolute;
    top: -50%;
    left: 50%;
    transform: translate(-50%);
    padding: 0 10px;
  }
  .under-line {
    position: relative;
    width: 100%;
    height: 20px;
    border-top: 1px solid #ccc;
    margin-top: 30px;
  }
  .under-line::before {
    content: '';
    font-size: 16px;
    color: #ccc;
    background-color: #fff;
    position: absolute;
    top: -50%;
    left: 50%;
    transform: translate(-50%);
    padding: 0 10px;
  }
}
</style>
