<template>
  <scroll class="left-scroll" ref="scroll">
    <div class="categroy-body-left">
      <ul class="category-box">
        <li
          v-for="item in category"
          :key="item.maitKey"
          class="category-item"
          :class="{ 'category-item-actived': item.maitKey === activedCategory }"
          @click="changeActivedCategory({ ...item })"
        >
          {{ item.title }}
        </li>
      </ul>
    </div>
  </scroll>
</template>

<script>
import Scroll from '@/components/common/scroll'

import { mapState, mapActions } from 'vuex'

export default {
  components: { Scroll },
  computed: {
    ...mapState({
      category: (state) => state.category.category,
      activedCategory: (state) => state.category.activedCategory
    })
  },
  created() {
    this.getCategoryData()
      .then(() => {
        return this.getSubCategoryData(this.category[0].maitKey)
      })
      .then(() => {
        this.getCateDetailData(this.category[0].miniWallkey)
        this.$refs.scroll.refresh()
      })
  },
  methods: {
    ...mapActions([
      'getCategoryData',
      'getSubCategoryData',
      'getCateDetailData',
      'changeActivedCategory'
    ])
  }
}
</script>

<style lang='less' scoped>
.left-scroll {
  width: 25vw;
  float: left;
  overflow: hidden;
}
.categroy-body-left {
  float: left;
  width: 25vw;
  background-color: #f6f6f6;
  .category-item {
    width: 100%;
    height: 50px;
    line-height: 50px;
    text-align: center;
  }
  .category-item-actived {
    background-color: #fff;
    border-left: 3px solid var(--color-high-text);
    color: var(--color-high-text);
    font-weight: 700;
  }
}
</style>
