<template>
  <div>
    <nav-bar class="category-nav"></nav-bar>
    <category-body></category-body>
    <main-tab-bar></main-tab-bar>
  </div>
</template>

<script>
import NavBar from '@/components/common/navbar/NavBar'
import MainTabBar from '@/components/content/MainTabBar'

import CategoryBody from './childrenComponents/CategoryBody'

export default {
  name: 'Category',
  components: { MainTabBar, NavBar, CategoryBody },
  data() {
    return {
      scrollY: 0
    }
  },

  activated() {
    document.documentElement.scrollTop = this.scrollY
    // 
    setTimeout(() => {
      document.documentElement.scrollTop = this.scrollY
    }, 1)
  },

  beforeRouteLeave(to, from, next) {
    this.scrollY = document.documentElement.scrollTop
    next()
  }
}
</script>

<style lang='less' scoped>
.category-nav {
  background-color: var(--color-tint);
  color: #fff;
}
</style>
